@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;600&family=Zen+Dots&display=swap');

.hero {
    width: 90%;
    height: 100vh;    
    margin: auto;
}

.hero .container {
    max-width: 1240px;
    padding: 0 1rem;
    margin: auto;
}

.hero .content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.hero .col-1 {
    padding: 1rem 1rem 1rem 0;
    height: 70vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin-right: 7%;
    
}

.hero .col-1 h1 {
    font-family: 'Zen Dots';
    font-style: normal;
    background: linear-gradient(89.88deg, #F6AD1D 15.86%, #E03173 79.81%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    font-size: 5rem;
    font-weight: 700;
    margin-top: 3%;
    margin-bottom: 3%;
}
.hero .col-1 h1 img{
    height: 10% 
}

.hero .col-1 p {
    font-size: 3rem;
    font-weight: 200;
    color: white;
    line-height: 1.4;
    margin-top: 0;
    text-align: justify;
}

.value{
    width: 95%;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    background: rgba(15, 6, 52, 0.2);
box-shadow: 0px 4px 42px rgba(0, 0, 0, 0.25) ;
backdrop-filter: blur(12.5px);
border-radius: 15px;
border-bottom: 2px solid #f8f8f8;
border-top: 2px solid rgba(161, 161, 216, 0.359);
margin-bottom: 10%;
}
